import _ from 'lodash';
import React, { Component } from 'react';
import './App.css';
import Highcharts from 'highcharts';
import ReactHighcharts from 'react-highcharts';
import Slider from 'rc-slider';
import Icon from 'react-oui-icons';

import constants from './constants';
import fns from './fns';
import minRegret from './data/min_regret_sim_results.json';
import minTime from './data/min_time_sim_results.json';
import uniform from './data/uniform_sim_results.json';

import 'rc-slider/assets/index.css';

class DistributionChart extends Component {
  getDistributionData = () => {
    const {
      distributions,
      position,
    } = this.props;

    const series = [
      {
        name: 'Original',
        data: [],
        animation: {
          duration: 0,
        },
      },
      {
        name: 'Variation 1',
        data: [],
        animation: {
          duration: 0,
        },
      },
      {
        name: 'Variation 2',
        data: [],
        animation: {
          duration: 0,
        },
      },
    ];
    distributions.forEach((distribution, index) => {
      series[0].data.push(index <= position ? distribution[0] : null);
      series[1].data.push(index <= position ? distribution[1] : null);
      series[2].data.push(index <= position ? distribution[2] : null);
    });
    return _.cloneDeep(series);
  }

  render() {
    const { policy } = this.props;

    const chartRef = React.createRef();

    const config = {
      credits: {
        enabled: false,
      },
      title: {
        text: constants.CHART_LABELS[policy],
      },
      series: this.getDistributionData(),
      yAxis: {
        title: {
          text: 'Distribution',
        },
      },
    }

    return (
      <div className="flex--1">
        <ReactHighcharts
          config={ config }
          ref={ chartRef }
        />
      </div>
    );
  }
}

class VariationCard extends Component {
  render() {
    const {
      conversionCount,
      title,
      visitorCount,
    } = this.props;

    return (
      <div className="flex flex--column flex-align--center soft--sides width--33">
        <h5>
          { title }
        </h5>
        <p className="micro muted">
          Conversions
        </p>
        <h2>
          { conversionCount }
        </h2>
        <p className="micro muted">
          Visitors
        </p>
        <h2>
          { visitorCount }
        </h2>
      </div>
    )
  }
}

class SummaryCard extends Component {
  getStatisticalSignificance = () => {
    const {
      counts,
      position,
    } = this.props;

    return 0.93;
  }
  getVisitorCount = (variation) => {
    const {
      counts,
      position,
    } = this.props;

    return counts[position].visitors[variation];
  }

  getConversionCount = (variation) => {
    const {
      counts,
      position,
    } = this.props;

    return counts[position].conversions[variation];
  }

  render() {
    return (
      <div className="border--left flex flex-align--center flex--column min-width--400">
        <div className="flex flex--row flex--space width--100">
          <VariationCard 
            conversionCount={ this.getConversionCount(0) }
            title="Original"
            visitorCount={ this.getVisitorCount(0) }
          />
          <VariationCard 
            conversionCount={ this.getConversionCount(1) }
            title="Variation 1"
            visitorCount={ this.getVisitorCount(1) }
          />
          <VariationCard 
            conversionCount={ this.getConversionCount(2) }
            title="Variation 2"
            visitorCount={ this.getVisitorCount(2) }
          />
          <VariationCard
            conversionCount={ this.getConversionCount(0) + this.getConversionCount(1) + this.getConversionCount(2) }
            title="Total"
            visitorCount={ this.getVisitorCount(0) + this.getVisitorCount(1) + this.getVisitorCount(2) }
          />
        </div>
        <h3 className="flex">
          Statistical Significance:
        </h3>
        <h1 className="flex">
          { this.getStatisticalSignificance() }
        </h1>
      </div>
    )
  }
}

class App extends Component {
  constructor(props) {
    super(props);

    this.minRegretDistributions = fns.getDistributions(minRegret);
    this.minTimeDistributions = fns.getDistributions(minTime);
    this.uniformDistributions = fns.getDistributions(uniform);
    this.minRegretCounts = fns.getCounts(minRegret);
    this.minTimeCounts = fns.getCounts(minTime);
    this.uniformCounts = fns.getCounts(uniform);
    this.maxVisitors = Math.max(this.minRegretDistributions.length, this.minTimeDistributions.length, this.uniformDistributions.length);
  }

  state = {
    currentPosition: 0,
    interval: null,
    multiplier: 5,
  };

  setCurrentPosition = (position) => {
    this.setState(() => {
      return {
        currentPosition: position
      }
    });
  }

  startCounting = () => {
    if (this.state.currentPosition >= this.maxVisitors - 1) {
      this.setState(() => {
        return {
          currentPosition: 0,
        };
      }, this.startInterval);
    } else {
      this.startInterval();
    }
  };

  startInterval = () => {
    this.setState((previousState) => {
      return {
        interval: setInterval(() => {
          if (this.state.currentPosition >= this.maxVisitors - 1) {
            this.stopCounting();
            return;
          }
          this.setState((_previousState) => {
            return {
              currentPosition: _previousState.currentPosition + _previousState.multiplier >= this.maxVisitors ? this.maxVisitors - 1 : _previousState.currentPosition + _previousState.multiplier,
            };
          });
        }, 10),
      }
    });
  }

  stopCounting = () => {
    clearInterval(this.state.interval);
    this.setState(() => {
      return {
        interval: null,
      };
    });
  };

  render() {
    return (
      <div className="soft--sides">
        <div className="push--ends flex flex--row flex-align--center width--100">
          <div className="push--right">
            { !this.state.interval && (
              <button
                className="no-padding"
                onClick={ this.startCounting }>
                <div className="flex square--50 flex-align--center flex-justify--center">
                  { this.state.currentPosition !== this.maxVisitors - 1 && (
                    <Icon
                      name="play"
                      size="large"
                    />
                  ) }
                  { this.state.currentPosition === this.maxVisitors - 1 && (
                    <Icon
                      name="refresh"
                      size="large"
                    />
                  ) }
                </div>
              </button>
            ) }
            { this.state.interval && (
              <button
                className="no-padding"
                onClick={ this.stopCounting }>
                <div className="flex square--50 flex-align--center flex-justify--center">
                  <Icon
                    name="pause"
                    size="large"
                  />
                </div>
              </button>
              
            ) }
          </div>
          <div className="flex flex--1 flex-align--center push--right">
            <Slider
              dots={ false }
              min={ 0 }
              max={ this.maxVisitors - 1 }
              onChange={ this.setCurrentPosition }
              value={ this.state.currentPosition }
            />
          </div>
        </div>
        <div className="flex flex--row border push--ends">
          <DistributionChart
            distributions={ this.uniformDistributions }
            policy={ constants.MANUAL }
            position={ this.state.currentPosition }
          />
          <SummaryCard
            counts={ this.uniformCounts }
            position={ this.state.currentPosition }
          />
        </div>
        <div className="flex flex--row border push--ends">
          <DistributionChart
            distributions={ this.minRegretDistributions }
            policy={ constants.MIN_REGRET }
            position={ this.state.currentPosition }
          />
          <SummaryCard
            counts={ this.minRegretCounts }
            position={ this.state.currentPosition }
          />
        </div>
        <div className="flex flex--row border push--ends">
          <DistributionChart
            distributions={ this.minTimeDistributions }
            policy={ constants.MIN_TIME }
            position={ this.state.currentPosition }
          />
          <SummaryCard
            counts={ this.minTimeCounts }
            position={ this.state.currentPosition }
          />
        </div>
      </div>
    );
  }
}

export default App;
